import React, { CSSProperties } from 'react'

type Props = {
  color?: string
  height: number
  style?: CSSProperties
  className?: string
}
export const Divider: React.FC<Props> = React.memo((props) => {
  const bgColor = props.color ?? 'var(--divide-bg-color)'
  return (
    <div
      className={props.className}
      style={{ ...props.style, backgroundColor: bgColor, height: props.height }}
    ></div>
  )
})
