import { Divider } from '@/components/divider'
import {
  useEventCallback,
  useFormItems,
  useForms,
  useStore,
  useUserFormStore
} from '@/hooks'
import { FormattedCommunityUserData, UserInfoItemConfig } from '@/types'
import { Form, Input, Modal } from 'antd-mobile'
import React from 'react'
import styles from './user.component.sass'
import { FormItemRenderMap } from './utils'

type Props = {
  index: number
  defaultValue: FormattedCommunityUserData
}
export const User: React.FC<Props> = React.memo((props) => {
  const [basics, others] = useFormItems()
  const [form] = Form.useForm()
  const { room } = useStore()
  const userFormStore = useUserFormStore()
  const formsStore = useForms(form)
  const onDelete = useEventCallback(() => {
    Modal.confirm({
      title: '删除住户',
      content: (
        <div className={styles['confirm-content']}>确定删除该住户吗？</div>
      ),
      onConfirm: async () => {
        if (room?.id) {
          userFormStore.deleteUser(room.id, props.index)
        }
      }
    })
  })
  // useEffect(() => {
  //   form.setFieldsValue(props.defaultValue)
  // }, [])
  // const otherInfo = userOtherInfo.render()

  const renderItems = (data: UserInfoItemConfig[]) => {
    return data.map((config) => {
      const Render = FormItemRenderMap[config.inputType]
      if (!Render) {
        return null
      }
      const render = () => {
        if (config.inputType === 'picker') {
          return (
            <Render
              form={form}
              config={config}
              defaultUserInfo={props.defaultValue}
            />
          )
        }
        return Render({ config, defaultUserInfo: props.defaultValue, form })
      }
      const rules =
        typeof config.rules === 'function'
          ? config.rules(form, props.index, formsStore)
          : config.rules
      return (
        <Form.Item
          validateTrigger="onBlur"
          key={config.key}
          name={config.key}
          label={config.name}
          rules={rules}
        >
          {render()}
          {/* <Render config={config} defaultUserInfo={props.defaultValue} /> */}
        </Form.Item>
      )
    })
  }

  const basicItems = renderItems(basics)
  const otherItems = renderItems(others)

  return (
    <div className={styles['wrapper']}>
      <Form
        form={form}
        layout="horizontal"
        footer={null}
        className={styles['form']}
        initialValues={props.defaultValue}
      >
        <div className={styles['header-wrapper']}>
          <span className={styles['header-name-wrapper']}>
            <div className={styles['user-icon']}></div>
            <span className={styles['header-name']}>住户{props.index + 1}</span>
          </span>
          <span className={styles['delete']} onClick={onDelete}>
            <div className={styles['delete-icon']}></div>
            删除
          </span>
        </div>
        <div className={styles['form-header']}>基本信息</div>
        {basicItems}
        <Form.Item hidden name="id">
          <Input type="hidden" />
        </Form.Item>
        <Divider
          color="var(--main-border-color)"
          height={1}
          className={styles['divider']}
        />
        <div className={styles['form-header']}>其他信息</div>
        {otherItems}
      </Form>
    </div>
  )
})
