import React, { useEffect } from 'react'
import { Button, Toast } from 'antd-mobile'
import { AddOutline } from 'antd-mobile-icons'
import { User } from '../user'
import {
  useEventCallback,
  useFormsStore,
  useStore,
  useUserFormStore
} from '@/hooks'
import styles from './user-list.component.sass'
import { Footer } from '../footer'
import { useLocation, useNavigate } from 'react-router-dom'
import { arrUnique } from '@/utils'
export const UserList: React.FC = React.memo(() => {
  const globalStore = useStore()
  const store = useUserFormStore()
  const formsStore = useFormsStore()
  const navigate = useNavigate()
  const location = useLocation()
  const userFormStore = useUserFormStore()

  const { users } = store
  const onSubmit = useEventCallback(async () => {
    const data = await formsStore.getData(globalStore.room!.id)
    if (arrUnique(formsStore.getIdCards()).length !== data.length) {
      Toast.show('存在重复的身份证号，请重新填写')
      return
    }
    const isUpdate = data?.some((user) => user.id > 0)
    if (!isUpdate) {
      await store.addCommunityUsers(data)
    } else {
      await store.updateCommunityUsers(data)
    }
    setTimeout(() => {
      navigate(location.pathname.replace('/update', ''))
    }, 200)
  })
  const onAddUser = useEventCallback(() => {
    userFormStore.addUser()
  })
  useEffect(() => {
    store.initialize()
  }, [])
  if (!users) {
    return null
  }
  const renderUsers = () => {
    return users?.map((user, index) => {
      return <User index={index} key={`${user.id}`} defaultValue={user} />
    })
  }
  const renderBtn = () => {
    return (
      <div className={styles['add-btn-wrapper']}>
        <Button
          onClick={onAddUser}
          block
          size="middle"
          color="primary"
          fill="outline"
        >
          <AddOutline />
          添加其他住户
        </Button>
      </div>
    )
  }
  return (
    <div className={styles['wrapper']}>
      {renderUsers()}
      {renderBtn()}
      <Footer onClick={onSubmit} />
    </div>
  )
})
