import React from 'react'
import {
  FormattedCommunityUserData,
  InputType,
  UserInfoItemConfig
} from '@/types'
import { Input, Selector, Picker, TextArea } from 'antd-mobile'
import styles from './user.component.sass'
import type { FormInstance } from 'antd-mobile/es/components/form'
import { useEventCallback } from '@/hooks'

type Props = {
  form: FormInstance
  config: UserInfoItemConfig
  defaultUserInfo: FormattedCommunityUserData
}

type PickerProps = Props & {
  value: string[]
  onChange?(...args: any[]): void
}

const RenderText: React.FC<Props> = (props) => {
  const { config } = props
  const onBlur = useEventCallback((e) => {
    config.onBlur?.(e.target.value, props.form)
  })
  return (
    <Input
      className={styles['input']}
      // defaultValue={(defaultUserInfo[key] as string) ?? ''}
      type="text"
      placeholder=""
      onBlur={onBlur}
    />
  )
}

const RenderTextarea: React.FC<Props> = (_props) => {
  return (
    <TextArea
      className={styles['input']}
      // defaultValue={(defaultUserInfo[key] as string) ?? ''}
      placeholder=""
    />
  )
}

const RenderNumber: React.FC<Props> = (_props) => {
  return (
    <Input
      className={styles['input']}
      // defaultValue={(defaultUserInfo[key] as string) ?? ''}
      type="number"
      placeholder=""
      min={0}
    />
  )
}

const RenderSelect: React.FC<Props> = (props) => {
  const { config } = props
  //
  return (
    <Selector
      // defaultValue={defaultValue as string[]}
      options={config.options ?? []}
    />
  )
}

const RenderPicker: React.FC<PickerProps> = (props) => {
  const { config } = props
  const onValueChange = (value: any[]) => {
    const v = value[0]
    if (v === null) return
    props.onChange?.(value)
  }
  return (
    <Picker
      // defaultValue={defaultUserInfo[key] as string[]}
      columns={config.options ?? []}
      onConfirm={onValueChange}
      value={props.value}
    >
      {(items, actions) => {
        const isEmpty = items.every((item) => item === null)
        const label = isEmpty ? (
          <div>请选择</div>
        ) : (
          items.map((item) => item?.label).join('')
        )
        return (
          <div className={styles['input']} onClick={actions.open}>
            {label}
          </div>
        )
      }}
    </Picker>
  )
}

export const FormItemRenderMap: Record<InputType, any> = {
  text: RenderText,
  number: RenderNumber,
  select: RenderSelect,
  picker: RenderPicker,
  textarea: RenderTextarea
}
