import React from 'react'
import { Layout } from '@/components/layout'
import { Divider } from '@/components/divider'
import { useCommunityUsers, UserInfoState, useStore } from '@/hooks'
import styles from './room-update.component.sass'
import { UserList } from './user-list'
import { useParams } from 'react-router-dom'

type Props = {
  // id: number
}

export const RoomUpdate: React.FC<Props> = React.memo((props) => {
  const store = useStore()
  const currenntLevel = 5
  const { id } = useParams()
  useCommunityUsers(id as any as number, false, (state: UserInfoState) => {
    if (!state.users?.length) {
      state.users = state.users || []
      state.users.push(state.defaultUser())
    }
  })
  return (
    <Layout
      level={currenntLevel}
      showSearch={false}
      showStatus={store.room?.toBeCollect}
    >
      <div className={styles['wrapper']}>
        <Divider height={7} />
        <UserList />
      </div>
    </Layout>
  )
})
